<template lang="pug">
    div.president-detail-content 
        //- div.developing ...
        mt-loadmore(
            :top-method="loadTop"
            :bottom-method="loadBottom"
            :bottom-all-loaded="paging[listType].allLoaded"
            :autoFill="autoFill"
            ref="loadmore"
            @top-status-change="handleTopChange"
            @bottom-status-change="handleBottomChange"
        )   
            mint-loadmore-top(:topStatus="topStatus" slot="top")
            //- -
            div.userInformation
                //- -
                div.state(v-if="data.type == 1")
                    span(v-if="data.gh_status == 0 && data.open_society_code == 0") : 
                    span(v-if="data.gh_status == 1 && data.open_society_code == 0") : 
                    span(v-if="data.gh_status == 2 && data.open_society_code == 0" @click="reapply") 
                    span(v-if="data.open_society_code == 1" v-text="`${data.society_code}`")
                //- -
                div.get-out(v-if="data.type == 0" @click="out()")
                    i.icon.iconfont &#xe683;
                //- - 
                div.state(v-if="data.type == 2")
                //- -
                div.join(v-if="data.type == 3" @click="join()")
                    i.icon.iconfont &#xe65b;
                //- -
                div.state(v-if="data.type == 4")
                     span : 
                //- -     
                div.state(v-if="data.type == 5")
                     span : 
                user-information(
                    :societyImage="data.society_image"
                    :societyName="data.society_name"
                    :societyChairman="data.society_chairman"
                    :userCount="data.user_count"
                    :fansCount="data.fans_count"
                    :societyExplain="data.society_explain"
                    :isLoaded="isLoaded"
                )
            
            //- -
            ul.president-title

                //- -
                template(v-if="data.type == 0")
                    li.member-info-text
                        span(:class="{'active' : listType == 'anchor'}" @click="getListType('anchor')") 
                    li.member-info-text
                        span(:class="{'active' : listType == 1}" @click="getListType('fans')") 

                //- -
                template(v-if="data.type == 1")
                    li.president-info-text
                        span(:class="{'active' : listType == 'anchor'}" @click="getListType('anchor')") 
                    li.president-info-text
                        span(:class="{'active' : listType == 'fans'}" @click="getListType('fans')") 
                    li.president-info-text
                        span(:class="{'active' : listType == 'member'}" @click="getListType('member')") 
                    li.president-info-text
                        span(:class="{'active' : listType == 'signOut'}" @click="getListType('signOut')") 

                //- -
                template(v-if="data.type == 2")
                    li.member-info-text
                        span(:class="{'active' : listType == 'anchor'}" @click="getListType('anchor')") 
                    li.member-info-text
                        span(:class="{'active' : listType == 'fans'}" @click="getListType('fans')") 

                //- -
                template(v-if="data.type == 3")
                    li.member-info-text
                        span(:class="{'active' : listType == 'anchor'}" @click="getListType('anchor')") 
                    li.member-info-text
                        span(:class="{'active' : listType == 'fans'}" @click="getListType('fans')") 

                //- -
                template(v-if="data.type == 4")
                    li.member-info-text
                        span(:class="{'active' : listType == 'anchor'}" @click="getListType('anchor')") 
                    li.member-info-text
                        span(:class="{'active' : listType == 'fans'}" @click="getListType('fans')") 

                //- -
                template(v-if="data.type == 5")
                    li.member-info-text
                        span(:class="{'active' : listType == 'anchor'}" @click="getListType('anchor')") 
                    li.member-info-text
                        span(:class="{'active' : listType == 'fans'}" @click="getListType('fans')") 
                        
            //- -listType                    
            div.anchorList(v-if="listType == 'anchor'") 
                anchor-list(:list="list.anchor" :isLoaded="isLoaded" :allLoaded="paging[listType].allLoaded" :type="data.type" v-on:chooseActionsData="chooseActionsData")

            div.fansList(v-if="listType == 'fans'") 
                fans-list(:list="list.fans" :isLoaded="isLoaded" :allLoaded="paging[listType].allLoaded")

            div.memberList(v-if="listType == 'member'")
                member-list(:list="list.member" :isLoaded="isLoaded" :allLoaded="paging[listType].allLoaded" v-on:ToUpdate="ToUpdate")

            div.signOutList(v-if="listType == 'signOut'")
                sign-out-list(:list="list.signOut" :isLoaded="isLoaded" :allLoaded="paging[listType].allLoaded" v-on:ToUpdate="ToUpdate")

            mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!paging[listType].allLoaded")
        mt-actionsheet(
            :actions="actionsData"
            v-model="isShowActionsheet"
        )
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import userInformation from '@/components/society/detail/userInformation'
import anchorList from '@/components/society/detail/anchorList'
import fansList from '@/components/society/detail/fansList'
import memberList from '@/components/society/detail/memberList'
import signOutList from '@/components/society/detail/signOutList'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
import { Toast, MessageBox, } from 'mint-ui'
export default {
    name: 'societyDetail',
    beforeRouteEnter(to, from, next) {   // 
        next(vm => {
            vm.setFooterBarActiveName('societyDetailIndex');
            $(".page-content").scrollTop(0);
        });
    },
    components: {
        userInformation,
        anchorList,
        fansList,
        memberList,
        signOutList,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    data() {
        return {
            data: '',
            list: {
                anchor: '',
                fans: '',
                member: '',
                signOut: ''
            },
            paging: {
                anchor: {
                    page: 2,
                    has_next: '',
                    allLoaded: false
                },
                fans: {
                    page: 2,
                    has_next: '',
                    allLoaded: false
                },
                member: {
                    page: 2,
                    has_next: '',
                    allLoaded: false
                },
                signOut: {
                    page: 2,
                    has_next: '',
                    allLoaded: false
                }
            },
            autoFill: false,
            allLoaded: false,
            isLoaded: false,
            topStatus: '',
            bottomStatus: '',
            society_id: '',
            isShowActionsheet: false,
            actionsData: [],
            currentUserId: '',
            listType: 'anchor',
            listWatch: 0,
            listCount: 0,
        }
    },
    created() {
        this.getData();
    },
    methods: {
        ...mapActions(['setFooterBarActiveName']),
        getData(isRefresh = false) {
            let self = this;
            self.society_id = self.$route.query.society_id;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_app_president' : self.api.society_app_president();
            self.axios.all([self.axios.get(axiosUrl + '&society_id=' + self.society_id + '&society_status=0'), self.axios.get(axiosUrl + '&society_id=' + self.society_id + '&society_status=1'), self.axios.get(axiosUrl + '&society_id=' + self.society_id + '&society_status=2'), self.axios.get(axiosUrl + '&society_id=' + self.society_id + '&society_status=3')])
                .then(self.axios.spread(function(anchor, fans, member, signOut) {
                    self.listWatch++;
                    self.isLoaded = true;
                    if (anchor.data.status == 1) {
                        if (anchor.data.type == 1) {
                            self.data = anchor.data;

                            self.list.anchor = anchor.data.list;
                            self.list.fans = fans.data.list;
                            self.list.member = member.data.list;
                            self.list.signOut = signOut.data.list;

                            self.paging.anchor = {
                                page: 2,
                                has_next: anchor.data.page.has_next,
                                allLoaded: anchor.data.page.has_next == 1 ? false : true
                            };
                            self.paging.fans = {
                                page: 2,
                                has_next: fans.data.page.has_next,
                                allLoaded: fans.data.page.has_next == 1 ? false : true
                            };
                            self.paging.member = {
                                page: 2,
                                has_next: member.data.page.has_next,
                                allLoaded: member.data.page.has_next == 1 ? false : true
                            };
                            self.paging.signOut = {
                                page: 2,
                                has_next: signOut.data.page.has_next,
                                allLoaded: signOut.data.page.has_next == 1 ? false : true
                            };
                            self.$nextTick(function() {
                                isRefresh && self.$refs.loadmore.onTopLoaded();
                            });
                        }
                        else {
                            self.data = anchor.data;

                            self.list.anchor = anchor.data.list;
                            self.list.fans = fans.data.list;
                            self.paging.anchor = {
                                page: 2,
                                has_next: anchor.data.page.has_next,
                                allLoaded: anchor.data.page.has_next == 1 ? false : true
                            };
                            self.paging.fans = {
                                page: 2,
                                has_next: fans.data.page.has_next,
                                allLoaded: fans.data.page.has_next == 1 ? false : true
                            };
                            self.$nextTick(function() {
                                isRefresh && self.$refs.loadmore.onTopLoaded();
                            });
                        }
                    }
                    else {
                        Toast(anchor.data.error || '');
                        isRefresh && self.$refs.loadmore.onTopLoaded();
                    }
                }))

                .catch(err => {
                    self.listWatch++;
                    Toast(err || '');
                    isRefresh && self.$refs.loadmore.onTopLoaded();
                });
        },
        save() {
            let self = this;
            if (self.listType == 'anchor') {
                self.listCount = 0;
                return true;
            }
            else if (self.listType == 'fans') {
                self.listCount = 1;
                return true;
            }
            else if (self.listType == 'member') {
                self.listCount = 2;
                return true;
            }
            else if (self.listType == 'signOut') {
                self.listCount = 3;
                return true;
            }
            else {
                return false;
            }
        },


        loadTop() {
            let self = this;
            if (self.save()) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_app_president' : self.api.society_app_president();
                self.axios.get(axiosUrl, {
                    params: {
                        society_id: self.society_id,
                        society_status: self.listCount
                    }
                })
                    .then(res => {
                        self.listWatch++;
                        self.isLoaded = true;
                        if (res.data.status == 1) {
                            self.data = res.data;
                            self.list[self.listType] = res.data.list;
                            self.paging[self.listType].page = 2;
                            self.paging[self.listType].has_next = res.data.page.has_next;
                            self.paging[self.listType].allLoaded = res.data.page.has_next == 1 ? false : true;
                        }
                        else {
                            Toast(res.data.error || '');
                        }

                        self.$nextTick(function() {
                            self.$refs.loadmore.onTopLoaded();
                        });
                    })
                    .catch(err => {
                        self.listWatch++;
                        Toast(err || '');
                        self.$refs.loadmore.onTopLoaded();
                    });
            }
        },
        loadBottom() {
            let self = this;
            if (self.save()) {
                if (self.paging[self.listType].has_next) {
                    let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_app_president' : self.api.society_app_president();
                    self.axios.get(axiosUrl, {
                        params: {
                            society_id: self.society_id,
                            society_status: self.listCount
                        }
                    })
                        .then(res => {
                            setTimeout(() => {
                                if (res.data.status == 1) {

                                    self.paging[self.listType].has_next = res.data.page.has_next;
                                    self.paging[self.listType].allLoaded = res.data.page.has_next == 1 ? false : true;
                                    self.paging[self.listType].page = self.paging[self.listType].page + 1;

                                    if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                        for (let i = 0; i < res.data.list.length; i++) {
                                            self.list[self.listType].push(res.data.list[i]);
                                        }
                                    }

                                }
                                else {
                                    Toast(res.data.error || '');
                                }
                                self.$nextTick(function() {
                                    self.$refs.loadmore.onBottomLoaded();
                                });
                            }, 500);
                        })
                        .catch(err => {
                            Toast(err || '');
                            self.$refs.loadmore.onBottomLoaded();
                        });
                }
                else {
                    return false;
                }
            }
        },
        getListType(listTypt) {
            this.listType = listTypt;
        },
        handleTopChange(status) {
            // 
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 
            this.bottomStatus = status;
        },
        //- 
        out() {
            let title = '';
            let message = "" + this.data.society_name;
            MessageBox.confirm(
                message,
                title
            )
                .then(action => {
                    let self = this;
                    let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_out' : self.api.society_out();
                    self.axios.get(axiosUrl, {
                        params: {
                            society_id: self.society_id,
                        }
                    }).then(res => {
                        if (res.data.status == 1) {
                            Toast('');
                            this.getData(true);
                        }
                        else {
                            Toast(res.data.error || '');
                        }
                    })
                        .catch(err => {
                            Toast(err || '');
                        });

                })
                .catch(err => {
                    console.log(err);
                })
        },
        //-
        join() {
            let title = '';
            let message = "" + this.data.society_name;
            MessageBox.confirm(
                message,
                title,
            )
                .then(action => {
                    let self = this;
                    let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_join' : self.api.society_join();
                    self.axios.get(axiosUrl, {
                        params: {
                            society_id: self.society_id
                        }
                    }).then(res => {
                        if (res.data.status == 1) {
                            Toast('');
                            this.getData(true);
                        }
                        else {
                            Toast(res.data.error || '');
                        }
                    })
                        .catch(err => {
                            Toast(err || '');
                        });
                })
                .catch(err => {
                    console.log(err);
                })
        },

        // 
        ToUpdate() {
            this.getData(true);
        },
        //- 
        reapply() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_app_reapply' : this.api.society_app_reapply();
            this.axios.get(axiosUrl, {
                params: {
                    society_id: this.society_id
                }
            }).then(res => {
                if (res.data.status == 1) {
                    Toast('');
                    this.getData(true);
                }
                else {
                    Toast(res.data.error || '');
                }
            })
                .catch(err => {
                    Toast(err || '')
                })
        },
        //- 
        outMember() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_member_del' : this.api.society_member_del();
            this.axios.get(axiosUrl, {
                params: {
                    member_id: this.currentUserId,
                    society_id: this.society_id
                }
            }).then(res => {
                if (res.data.status == 1) {
                    Toast('');
                    this.getData(true);
                }
                else {
                    Toast(res.data.error || '');
                }
            })
                .catch(err => {
                    Toast(err || '');
                })
        },
        showActionsheet() {
            this.isShowActionsheet = !this.isShowActionsheet;
        },
        chooseActionsData(IdentityType, userId) {
            this.showActionsheet();

            this.currentUserId = userId;
            if (IdentityType == 'member') {
                this.actionsData = this.userDetailsData;
            }
            else {
                this.actionsData = this.othersDetailsData;
            }
        }
    },
    computed: {
        othersDetailsData() {
            // 
            let self = this, arrOne = [{
                name: '',
                method: function() {
                    self.$router.push({ path: '/user/others', query: { to_user_id: self.currentUserId } });
                }
            }, {
                name: '',
                method: function() {
                    self.outMember();
                }
            }];
            return arrOne;
        },
        userDetailsData() {
            // 
            let self = this, arrTwo = [{
                name: '',
                method: function() {
                    self.$router.push({ path: '/user/others', query: { to_user_id: self.currentUserId } });
                }
            }];
            return arrTwo;
        }
    }
}
</script>
<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.president-detail-content {
    .userInformation {
        position: relative; //
        .state {
            position: absolute;
            top: .65rem;
            right: .5rem;
            color: #fff;
            z-index: 999;
        } // 
        .get-out {
            position: absolute;
            top: .4rem;
            right: .4rem;
            z-index: 999;
            .iconfont {
                font-size: 1rem;
                color: #fff;
            }
        } //
        .join {
            position: absolute;
            top: .4rem;
            right: .4rem;
            z-index: 999;
            .iconfont {
                font-size: 1rem;
                color: #fff;
            }
        }
    }
    .president-title {
        border-bottom: 1px solid #e6e8f1;
        display: flex; //
        .president-info-text {
            background-color: #fff;
            width: 25%;
            text-align: center;
            font-size: .7rem;
            padding: .775rem 0;
            position: relative;
        } //
        .member-info-text {
            background-color: #fff;
            float: left;
            width: 50%;
            text-align: center;
            font-size: .7rem;
            padding: .775rem 0;
            position: relative;
        }
        li:after {
            content: '';
            display: block;
            height: .8rem;
            width: 1px;
            background-color: #e6e8f1;
            position: absolute;
            left: -2px;
            top: .8rem;
        }
        span {
            padding: 0 .5rem .8rem .5rem;
            color: @fc-light;
            white-space: nowrap;
        }
        .active {
            border-bottom: 2px solid #333;
            color: @color-theme;
            padding: 0 .5rem .8rem .5rem;
            white-space: nowrap;
        }
        .sigu-out {
            span {
                border: 0;
            }
        }
    }
}
</style>




